<template>
    <div class="orderDetail">
        <header-btn title="方案信息">
            <div slot="btn">
                <Button v-if="detailData.orderStatus=='已下发'" @click="openDialog">拒绝订单</Button>
                <Button v-if="detailData.orderStatus=='已下发'" type="primary" @click="accept">接受订单</Button>
            </div>
        </header-btn>
        <div class="main-box">
            <div class="route-box">
                <cart-route :routeData="detailData"></cart-route>
            </div>
            <div class="time">
                <span>{{detailData.createEtpsName}}</span>
                <span>计划时间：{{moment_time_m(detailData.planStartTime)}} -- {{moment_time_m(detailData.planEndTime)}}</span>
            </div>
            <div class="public-line1jz" style=" background: #999;"></div>
            <div class="chang-jing">
                <scene-map :List="sceneMapResKList" @auditStatus="handelValue"></scene-map>
            </div>
            <Divider :dashed="true" style="border-color: #999;" />
            <!-- 基础任务 -->
            <div class="list-item" v-if="BasicTaskList.length>0">
                <div class="tit">
                    基础任务内容：
                    <span>
                        (服务总价: ￥
                        <span>{{BasicTaskList[0].taskCost}} / {{BasicTaskList[0].goosUnit}}</span>
                        <span>总时长：{{BasicTaskList[0].serverDuration}}{{BasicTaskList[0].serverDurationUnit}}</span>)
                    </span>
                </div>
                <div class="main">
                    <span
                        class="item"
                        v-for="(item,index) in BasicTaskList"
                        :key="index"
                    >{{item.taskName}}</span>
                </div>
            </div>
            <!-- 附加任务 -->
            <div class="list-item" v-if="AdditionalTaskList.length>0">
                <div class="tit">附加任务内容：</div>
                <div class="main">
                    <span class="item" v-for="(item,index) in AdditionalTaskList" :key="index">
                        {{item.taskName}}
                        (价格: ￥
                        <span
                            class="i-i"
                        >{{item.taskCost}} / {{item.goosUnit}}</span>
                        <span class="i-i">总时长：{{item.serverDuration}}{{item.serverDurationUnit}}</span>)
                    </span>
                </div>
            </div>
            <!-- 交互 -->
            <order-status ref="orderStatusId" :detailData="detailData"></order-status>
        </div>
    </div>
</template>

<script>
import cartRoute from './cart-route/index'
import headerBtn from './header-btn/index'
import {
    apiGetOrderDetails,
    apiModifyOrderStatus,
} from '@/api/logisticsCommission'
import dictionaryList from '@/mixins/dictionaryList'
import sceneMap from './scene-map/index'
import orderStatus from './order-status'
export default {
    name: '',
    mixins: [dictionaryList],
    components: {
        cartRoute,
        headerBtn,
        sceneMap,
        orderStatus
    },
    data() {
        return {
            detailData: {},
            // 场所名
            sceneMapResKList: [],
            // 基础任务集合
            BasicTaskList: [],
            // 附加任务集合
            AdditionalTaskList: [],
        }
    },
    computed: {
        cptOrderCode() {
            return this.$route.query.orderCode
        },
    },
    created() {
        this.getDetail()
    },
    mounted() {},
    methods: {
        //拒绝
        openDialog(){
            this.$refs.orderStatusId.openDialog()
        },
        // 接受
        accept(){
            this.$refs.orderStatusId.accept()
        },
        handelValue(key) {
            if (key == '') {
                this.BasicTaskList = []
                this.AdditionalTaskList = []
                return
            }
            this.BasicTaskList = this.detailData.sceneMapRes[key].filter(
                (item) => item.taskType == 'B'
            )
            this.AdditionalTaskList = this.detailData.sceneMapRes[key].filter(
                (item) => item.taskType == 'A'
            )
        },
        getDetail() {
            // 查询详情
            apiGetOrderDetails({
                orderCode: this.cptOrderCode,
            }).then(({ data }) => {
                this.detailData = data
                this.sceneMapResKList = Object.keys(data.sceneMapRes)
            })
        },
    },
}
</script>

<style scoped lang="scss">
.orderDetail {
    .route-box {
        margin-bottom: 20px;
    }
    .main-box {
        padding: 20px;
        background: #fff;
        box-shadow: 0px 2px 4px 0px rgba(51, 51, 51, 0.1);
        min-height: 80vh;
    }
    .list-item {
        margin-bottom: 16px;
        .tit {
            font-size: 14px;
            font-weight: bold;
            line-height: 28px;
            span {
                font-size: 12px;
                font-weight: 400;
                margin-right: 20px;
            }
        }
        .main {
            background: rgba(249, 249, 249, 1);
            height: 53px;
            display: flex;
            align-items: center;
            .item {
                margin-left: 20px;
                margin-right: 100px;
                .i-i {
                    margin-right: 20px;
                }
            }
        }
    }
    .chang-jing {
        margin-top: 20px;
    }
    .time {
        span {
            &:nth-child(1) {
                font-size: 16px;
                font-weight: bold;
                color: rgba(51, 51, 51, 1);
                line-height: 28px;
                padding-right: 65px;
            }
        }
    }
}
</style>
